import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { SystemAdmin } from "./systemadmin.component";
import { SystemConfiguration } from "./systemconfiguration/systemconfiguration.component";
import { CanActivateTeam } from "../../common/services/permission";
import { routing } from "./systemadmin.routing";
import { PrintFileSetup } from "./systemconfiguration/printfilesetup.component";
import { SettingMaster } from "./systemconfiguration/settingmaster.component";
import { DivisionMaster } from "./systemconfiguration/divisionmaster.component";
import { TaxConfigCharges } from "./systemconfiguration/taxconfigcharges.component";
import { ResetToDefult } from "./systemconfiguration/resettodefault.component";
import { AddDivision } from "./systemconfiguration/adddivision.component";
import { AllSetting } from "./systemconfiguration/allsetting.component";
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { GenericPopupGridModule } from "../../common/popupLists/generic-grid/generic-popup-grid.module";
import { OtpAuth } from "./systemconfiguration/otpAuth.component";
import { ActivatedRoute } from "@angular/router";
import { DiviService } from "./systemconfiguration/divi.service";
import { SearchPipe } from "./systemconfiguration/search.pipe";

@NgModule({
    imports:[
        CommonModule,
        FormsModule,
        routing,
        ReactiveFormsModule,
        NgMultiSelectDropDownModule.forRoot(),
        GenericPopupGridModule.forRoot()
    ],
    declarations:[
        SystemAdmin, 
        SystemConfiguration, 
        PrintFileSetup, 
        SettingMaster,
        ResetToDefult,
        DivisionMaster,
        TaxConfigCharges,
        AddDivision,
        OtpAuth,
        SearchPipe
    ],
    providers:[CanActivateTeam, DiviService]
})

export class SystemAdminModule{}



