import { Component, OnInit, ViewChild } from "@angular/core";
import { IDropdownSettings } from "ng-multiselect-dropdown/multiselect.model";
import { DiviService } from "./divi.service";
import { AlertService } from "../../../common/services/alert/alert.service";
import { OtpAuth } from "./otpAuth.component";


@Component({
    selector: 'resetdefault',
    template: require('./resettodefault.component.html')
})

export class ResetToDefult implements OnInit{
  dropdownList = [];
  selectedItems = [];
  dropdownSettings:IDropdownSettings={};
  default: any;
  isOtpVisible: boolean;
  @ViewChild("OTPpopUp") OtpPopUp: OtpAuth;

  toggleOtpPopup() {
    this.OtpPopUp.showOTPpopUp();
  }

  hideOtpPopup(value: string) {

    if (value === "Authentication successful!") {
      this.reset();
    } else {
     return;
    }
  }

  ngOnInit() {

    this.dropdownSettings= {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 5,
      allowSearchFilter: true,
      
    };
    this.dropdownList = [
      { item_id: 'formwiseprintformat', item_text: 'Formwise Print Format'},
      { item_id: 'reportcolumnformat', item_text: 'Report Column Format' },
      { item_id: 'storedprocedure', item_text: 'Update Stored Procedure / Views' },
      { item_id: 'taxchargesconfig', item_text: 'Tax Config Charges' },
      { item_id: 'reportmaster', item_text: 'Default Report Master & Report Details'},
      { item_id: 'directorapp', item_text: 'Directors app Stored Procedure'},
       {item_id: 'reportcontextmenu', item_text: 'DrillDown'},
    ];

  }
  constructor(private resetService: DiviService, private alertService:AlertService){}

  reset(){
    let printFormat= [];
    this.selectedItems.forEach((x) => 
    {
      printFormat.push(x.item_id);
    })
    this.resetService.resetDefault(printFormat).subscribe(data =>{
      if (data.status=='ok'){
        this.alertService.success(data.result);
      }else{
        this.alertService.error(data.result._body);
      }
      this.selectedItems=[];
    },error => {
      this.alertService.error(error);
    });
  }

  onItemSelect(item: any) {
    console.log(item);
  
  }
  onSelectAll(items: any) {
    console.log(items);
  }
}



