import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { DiviService } from "./divi.service";
import { AlertService } from "../../../common/services/alert/alert.service";

@Component({
    selector: 'taxconfigcharges',
    template: require('./taxconfigcharges.component.html')
})

export class TaxConfigCharges implements OnInit{
  configItems :any []= [];
  checkTaxList: any;

  ngOnInit(): void {
      this.loadTaxconfig();
  }

      constructor(private router: Router, private taxconfig: DiviService, private route: ActivatedRoute, private alertservice:AlertService){}
      goback(){
        this.router.navigate(["pages/dashboard/dashboard"])
       }
       
       loadTaxconfig() {
        this.taxconfig.getTaxConfig().subscribe(data =>{
          this.configItems=data.result;
        },error => {
          console.error('Error fetching permissions', error);
        });
       }
       preventInput($event) {
        $event.preventDefault();
        return false;
      }
    
      checkList(acid: any){
        if(acid==null){
          acid="";
        }
        this.taxconfig.getcheckList(acid).subscribe(data =>{
         this.alertservice.error(data.result._body);
        },error => {
         console.error('Error fetching Acid', error);
       });
      }
}


