import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { DiviService } from "./divi.service";
import { AnonymousSubscription } from "rxjs/Subscription";
import { AlertService } from "../../../common/services/alert/alert.service";
import { AppConfiguration, environment } from "../../../environment";
import { Spinner } from "primeng/primeng";
import { SpinnerService } from "../../../common/services/spinner/spinner.service";
import { data } from "jquery";
import { OtpAuth } from "./otpAuth.component";

@Component({
    selector: 'printfilesetup',
    template: require('./printfilesetup.component.html'),
})

export class PrintFileSetup implements OnInit{
  isModalVisible = false;
  @ViewChild('modalcheckbox') checkbox: ElementRef; 
  @ViewChild("OTPpopUp") OtpPopUp: OtpAuth;
  isOtpVisible :boolean=false;
  fileName: any[]=[];
  vouchertype: any[]=[];
  defaultPrintFile: any;
  selectedVoucherType: any;
  BtnClicked:string;
  fileList: FileList=null;
  
  
ngOnInit(): void {
    this.loadVoucherType();
}


  toggleOtpPopup(btnClicked:string) {
    this.BtnClicked = btnClicked;
    this.OtpPopUp.showOTPpopUp();
  }

  hideOtpPopup(value: string){
    if(value === "Authentication successful!"){
        switch(this.BtnClicked){
          case "RESETTODEFAULT" : this.reset();
          break;
          case "UPLOAD": this.confirmationUploadClicked(true);
          break;
        }
        
      }else{
        return;
      }
      console.log("emitted value",value);
  }

  toggleModal(event: Event) {
    const checkbox = event.target as HTMLInputElement;
    this.isModalVisible = checkbox.checked;
    this.Service.getPrintFileName().subscribe(data =>{
      this.fileName=data.result;
    },error => {
      console.error('Error fetching permissions', error);
    });
  }

  hideModal() {
    this.isModalVisible = false;
    const checkbox = this.checkbox.nativeElement as HTMLInputElement;
    checkbox.checked = false;
    console.log('Hiding modal');
  }

  constructor(private router: Router, private Service:DiviService, private alertservice: AlertService, private loadingService:SpinnerService) {}

  goback() {
    this.router.navigate(['pages/dashboard/dashboard']);
  }

  loadVoucherType(){
    this.Service.getVoucherType().subscribe(data =>{
      this.vouchertype=data;
    },error => {
      console.error('Error fetching Voucher Type', error);
    });
  }

  // onDefaultPrintFile(){
  //   this.Service.getDefaultPrintFile(this.selectedVoucherType).subscribe(data =>{
  //     if (data.status === 'ok') {
  //       this.defaultPrintFile=data.result;
  //     }
  //   },error => {
  //     console.error('Error fetching File', error);
  //   });
  // }
 
  reset(){
    this.Service.resetVoucherType(this.selectedVoucherType).subscribe(data =>{
      if (data.status='ok'){
        this.alertservice.success(data.result);
      }
      else{
        this.alertservice.error(data.result._body);
      }
      // this.selectedVoucherType=[];
    },error => {
      console.error('Error fetching permissions', error);
    });
  }
  resetAll(){
    this.Service.resetAllToDefault().subscribe(data =>{
      if(data.status='ok'){
        this.alertservice.success("PrintFile is successfully reset to default");
      }else{
        this.alertservice.error(data.result._body);
      }
    },error => {
      console.error('Error fetching permissions', error);
    });
  }

  downloadJS(printfilename){
    var url =AppConfiguration.apiUrl+`/downloadPrintFilesForSystemUser/${printfilename}`
    fetch(url)
      .then(resp => resp.blob())
      .then(blob => {
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.style.display = 'none';
        a.href = url;
        // the filename you want
        a.download = printfilename
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        this.alertservice.success('Your file has downloaded!'); 
      })
      .catch(() => alert('Download Failed'));
    }

downloadDefaultFile() {
  this.Service.getDefaultPrintFile(this.selectedVoucherType).subscribe(
    (res: any) => {
      if(res.status=='ok'){
        let printfilename = res.result;
        this.downloadJS(printfilename);
      }
  else{
    this.alertservice.error(res.result);
  }
    },
    error => {
      console.log("w",error)
      this.alertservice.error(error.error.result);
      this.loadingService.hide();
})
}

onFileChange($event){
  this.fileList = $event.target.files;
}
confirmationUploadClicked(event:boolean){
  if(event == true){
    console.log("FILE LIST", this.fileList);
  if (this.selectedVoucherType == null || this.selectedVoucherType == "") {
    this.alertservice.info("Please Select Master.");
    return;
  }

  if (this.fileList == null || this.fileList.length == 0) {
    this.alertservice.info("Please Select File.");
    return;
  }

  if (this.fileList.length > 0) {
    let file: File = this.fileList[0];
    let formData: FormData = new FormData();
    formData.append("config", file, file.name);

    this.loadingService.show("Uploading. Please Wait...this may take some time");
    this.Service.uploadPrintFile(formData, this.selectedVoucherType).subscribe(
      res=>{
        console.log("result from print file upload",res);
        this.loadingService.hide();    
            console.log("result");
            if (res.status == "ok") {
              this.alertservice.success("Request Successful");
              this.selectedVoucherType.VOUCHER_NAME = "";
              this.fileList = null;
              //this.fileSelector_Import.nativeElement.value = null;
              // this.PrintFileUpload.nativeElement.value = null;
            } else if (res.status == "errorfile") {
              this.alertservice.error("Some of the rows cannot be saved. Please check 'Upload Status' for more details.");
            } else if (res.status == "error") {
                this.alertservice.error("Error :"+res.result.error);
            }
            else {
              this.loadingService.hide();
              this.alertservice.error(res.result);
            }
      }, 
      error =>{
        this.loadingService.hide();
        this.alertservice.error(error);
      }
    )
    
  }
  }else {
    this.alertservice.error("OTP Invalid");
}
}
}



