import { Router } from '@angular/router';
import { Component, OnInit, ViewChild } from '@angular/core';
import { OtpAuth } from './otpAuth.component';
import { DiviService } from './divi.service';
import { SpinnerService } from '../../../common/services/spinner/spinner.service';
import { AlertService } from '../../../common/services/alert/alert.service';

@Component({
  selector: 'settingmaster',
  template: require('./settingmaster.component.html')
})

export class SettingMaster implements OnInit{

  modeTitle: string;
  activerowIndex: number = 0;
  searchSetting: any;
  isActiveSettingList: boolean;
  isActiveSettingData: boolean;
  currentChoiceCat: any;
  currentChoiceList: any;
  categoryName: any;
  settingName: any;
  settingDetails: any;
  modeName: string;
  moduleArray: any;
  allSettings: any;
  currentCatName: any;
  currnetSetting: any;
  showCategoryWiseSetting: boolean = true;
  showAllSetting: boolean = false;

  @ViewChild("OTPpopUp") OtpPopUp: OtpAuth;

  constructor(
    private alertService: AlertService,
    private loadingService: SpinnerService,
    private router: Router,
    private masterSettingService: DiviService,
  ) {
    this.searchSetting = 'searchName';
    this.loadingService.show("Loading setting data...")
        this.masterSettingService.getSettingDes().subscribe((res: any) => {
          if (res) {
            this.loadingService.hide();
            if (res.status == "ok") {
              this.allSettings = res.result;
              this.filterCategory();
            } else {
              this.alertService.error(res.result)
            }
          }
        }, error => {
          this.loadingService.hide();
        })
  }

  hideOtpPopup(value: string) {

    if (value === "Authentication successful!") {
     this.confirmationOkClicked(true);
    } else {
     return;
    }
  }
  filterCategory() {
    let result = this.allSettings
    let arr = [];
    for (let i = 0; i < this.allSettings.length; i++) {
      let catgories = (result[i].CATEGORY)
      arr.push(catgories)
    }
    this.categoryName = Array.from(new Set(arr.filter(item => item === 'Account Settings')));
  }

  ngOnInit(): void {
    this.modeTitle = "Master Setting Info";
  }

  categoryClick(catName: string) {
    this.isActiveSettingList = true;
    this.filterModule(catName);
    if (catName !== (this.settingName && this.settingName.CATEGORY)) {
      this.isActiveSettingData = false;
    }
    this.currentChoiceCat = catName;
  }

  filterModule(catName: string) {
    this.currentCatName = catName;
    let moduleList = this.allSettings.filter(x => x.CATEGORY == catName && x.MODULE);
    let arr = [];
    for (let i = 0; i < moduleList.length; i++) {
      let module = moduleList[i].MODULE;
      arr.push(module);
    }
    this.moduleArray = Array.from(new Set(arr));
    this.settingName = moduleList;
  }

  settingClick(setName: string) {
    this.currnetSetting = setName;
    this.isActiveSettingData = true;
    let setting = this.settingName.filter(x => x.MODULE == setName);
    this.settingDetails = setting;
    for (var i = 0; i < (this.settingDetails ? this.settingDetails.length : 0); i++) {
      if (this.settingDetails[i] && this.settingDetails[i].DATA_TYPE && this.settingDetails[i].DATA_TYPE.toLowerCase() === 'select') {
        this.settingDetails[i].POSSIBLE_VALUES_LIST = this.settingDetails[i].POSSIBLE_VALUES.split(',');
      }
    }    
    this.currentChoiceList = setName;
  }


  getActiveCat(catName: string): string {
    if (this.currentChoiceCat === catName) {
      return 'active';
    }
    else {
      return 'not';
    }
  }

  getActive(choiceList: string): string {
    if (this.currentChoiceList === choiceList) {
      return 'activeList';
    }
    else {
      return 'not';
    }
  }

  onUpdate() {    
    this.OtpPopUp.showOTPpopUp();
    this.modeName = "";
  }

  onInvUpdate(settingData: any){
    let arr=[];
    arr.push(settingData)
    this.settingDetails = arr;
    this.OtpPopUp.showOTPpopUp();
    this.modeName = "";
  }

  onInvReset(settingData: any){
    let arr=[];
    arr.push(settingData)
    this.settingDetails = arr;
    this.OtpPopUp.showOTPpopUp();
    this.modeName = 'reset';
  }

  confirmationOkClicked(event: boolean) {
    if (event == true) {
      if (confirm("Are you sure you want to save?") == true) {
        this.loadingService.show("Updating...");
        this.masterSettingService.updateSettingDes(this.settingDetails, this.modeName).subscribe((res: any) => {
          if (res) {
            this.loadingService.hide();
            if (res.status == "ok") {
              this.alertService.success(res.result)
              this.isActiveSettingData = false;
              this.loadingService.show("Loading setting...")
              this.masterSettingService.getSettingDes().subscribe((res: any) => {
                if (res) {
                  this.allSettings = res.result;
                  this.filterCategory();
                  this.filterModule(this.currentCatName);
                  this.settingClick(this.currnetSetting);
                  this.loadingService.hide();
                  this.isActiveSettingData = true;
                } else {
                  this.loadingService.hide();
                  this.alertService.warning(res.result);
                }
              })
            } else {
              this.loadingService.hide();
              this.alertService.warning(res.result);
            }
          } else {
            this.loadingService.hide();
            this.alertService.warning(res.result);
          }
        })
      }
    } else {
      this.alertService.error("OTP Invalid");
    }
  }

  allSetting() {
    if (this.allSettings) { 
      for (let i = 0; i < this.allSettings.length; i++) {
        let setting = this.allSettings[i];
       if (setting && setting.CATEGORY === 'Account Settings') {
    if (setting.DATA_TYPE && setting.DATA_TYPE.toLowerCase() === 'select') {
      setting.POSSIBLE_VALUES_LIST = setting.POSSIBLE_VALUES ? setting.POSSIBLE_VALUES.split(',') : [];
  }
}
      }
    }
    this.showCategoryWiseSetting = false;
    this.showAllSetting = true;
  }
//   allSetting() {
//     if (this.allSettings) { 
//       for(let i=0; i< ) {
//             let setting = this.allSettings[i];
            
//         }
//     }
//     this.showCategoryWiseSetting = false;
//     this.showAllSetting = true;
// }

  

  categoryWiseSetting(){
    this.showCategoryWiseSetting = true;
    this.showAllSetting = false;
  }

  setToDefault() {
    this.OtpPopUp.showOTPpopUp();
    this.modeName = "reset";
  }

  inputChange() {
    this.isActiveSettingData = true;
    this.isActiveSettingList = true;
  }

  cancel() {
    try {
      this.router.navigate(["./pages/dashboard"]);
    } catch (ex) {
      alert(ex);
    }
  }

  resetAllNormalSetting(){
    this.settingDetails = this.allSettings.filter(x => x.MODULE != 'Mandatory Settings');
    this.OtpPopUp.showOTPpopUp();
    this.modeName = 'reset';
  }
}


