import { Component, EventEmitter, Output } from "@angular/core";
import { DiviService } from "./divi.service";
import { AlertService } from "../../../common/services/alert/alert.service";

@Component({
    selector: 'otpAuth',
    template: require('./otpAuth.component.html'),
})

export class OtpAuth{
    @Output() hideOtp = new EventEmitter<string>();

    authOTP:any;
    isOtpVisible:boolean=false;

    constructor(private service:DiviService, private alertService: AlertService){}
    
  closeOtp(value: string | null) {
    this.isOtpVisible = false;
    this.hideOtp.emit(value || "");  
    this.authOTP="";
  }


    showOTPpopUp(){
        this.isOtpVisible = true;
    }
onSubmit(){
this.service.authOTP(this.authOTP).subscribe(data =>{
    if (data.status=='ok'){
      this.alertService.success(data.result);
      this.closeOtp(data.result);
    }else{
        this.alertService.error(data.result._body);
      }
      this.authOTP="";
    },error => {
  this.alertService.error(error.result._body);
    this.closeOtp(null);
  });
}

}


