import { Component } from "@angular/core";
import { Router } from "@angular/router";
import { DiviService } from "./divi.service";
import { AlertService } from "../../../common/services/alert/alert.service";

@Component({
    selector: 'divisionmaster',
    template: require('./divisionmaster.component.html')
})

export class DivisionMaster{

  divisionList: any[] = [];
  subcriptions: any;
  searchedTerm:any;
  settings = {
    mode: "external",
    add: {
      addButtonContent: '',
    },
    view: {
      viewButtonContent: 'View',
      saveButtonContent: '<i class="ion-checkmark"></i>',
      cancelButtonContent: '<i class="ion-close"></i>',
    },
    edit: {
      editButtonContent: 'Edit',
      saveButtonContent: '<i class="ion-checkmark"></i>',
      cancelButtonContent: '<i class="ion-close"></i>',
    },
    delete: {
      deleteButtonContent: '',
    },
    pager: {
      display: true,
      perPage: 14
    },
    columns: {
      INITIAL: {
        title: 'Initial',
        type: 'string'

      },
      NAME:{
        title:'Name',
        type: 'string'
      },
      ADDRESS2: {
        title: 'ADDRESS',
        type: 'string'
      },
      REMARKS: {
        title: 'Remarks',
        type: 'string'

      }
      ,
      ACNAME: {
        title: 'Branch Account',
        type: 'string'

      }
      ,
      RateGroup_Name: {
        title: 'Rate Group List',
        type: 'string',
        width: '20px'
      }
      ,
      COSTCENTER: {
        title: 'Cost Center',
        type: 'string',
        width: '20px'
      }
    }
  };

    constructor(private router: Router, private divisionService : DiviService, private alertService:AlertService){
    }

   ngOnInit(): void {
    this.loadDivisions();
   } 

 loadDivisions() {
  this.divisionService.getDivisions().subscribe(data =>{
    this.divisionList=data.result;
  },error => {
    console.error('Error fetching permissions', error);
  });
 }
  
 goback(){
  this.router.navigate(["pages/dashboard/dashboard"])
 }

 addDivi() {
  this.router.navigate(['pages/systemadmin/adddivision', { mode: "add", returnUrl: this.router.url }]);
}

onView(division){
  this.router.navigate(["pages/systemadmin/adddivision", { Initial: division.INITIAL, mode: "view", returnUrl: this.router.url }]);
}

onEdit(division):void{
 
  this.router.navigate(["pages/systemadmin/adddivision", { Initial: division.INITIAL, mode: "edit", returnUrl: this.router.url }]);
    
}

onDelete(division):void{
  if (window.confirm('Are you sure you want to delete?')) {
    this.divisionService.deleteDivision(division.INITIAL).subscribe((data:any)=>{
      if(data.status=="ok"){
        this.alertService.success(data.result);
        this.loadDivisions()
      }
    })

  } else {
    division.confirm.reject();
  }

}
}


